.seeMore {
  font-size: .85rem;
}

.infoIcon {
  width: 50%;
  height: 50%;
}

.commentText {
  visibility: hidden;
  opacity: 0;
  z-index: 1050;
  width: 50vw;
  transition: opacity .5s, visibility .5s;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
}

.comment {
  display: inline-block;
}

.comment img {
  cursor: pointer;
}

.comment img:hover + .commentText {
  visibility: visible;
  opacity: 1;
}

@media (width <= 992px) {
  .commentText {
    width: 50vw;
  }
}

@media (width <= 768px) {
  .commentText {
    width: 75vw;
  }
}

@media (width <= 576px) {
  .commentText {
    width: 90vw;
  }
}
